    padding: 30px;
    box-sizing: border-box;
    font-family: NotoSans, sans-serif;
    text-decoration: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    overflow: hidden;
    margin: 10px;
  }

  .news .news-blocks .news-block span.img {
    height: inherit;
    display: block;
    background-size: cover;
    background-position: center;
    margin: -30px;
    width: 75%;
    -webkit-transition: .5s ease;
    transition: .5s ease;
  }

  .news .news-blocks .news-block span.img:before {
    content: "";
    background: -webkit-linear-gradient(right, #725d3e 0, transparent 102%);
    background: linear-gradient(270deg, #725d3e 0, transparent 102%);
    width: 101%;
    height: inherit;
    display: block;
  }

  .news .news-blocks .news-sliced {
    width: 100%;
    margin: 0 10px;
    flex-direction: column;
    justify-content: normal;
  }

  .news .news-blocks .news-sliced .news-block-sliced {
    width: 100%;
    margin: 10px 0;
  }
}

